body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  font: 62.5%;
  font-family: 'Courier New', Courier, monospace, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

#root{
  height: 100%;
}

/* layout */

.grid-container{
  display: grid;
  grid-template-areas: 
      'header'
      'main'
      'footer';
  grid-template-columns: 1fr ;/*1fr means 1 column no sidebars nothing 1fr stands for 1 fragment*/
  grid-template-rows: 5rem 1fr 5rem ;/*1 rem = 10px*/
  height: 100%;
}

header{
  grid-area: header;
  background: #203040;
}
main{
  grid-area: main;
  padding: 1rem;
}
footer{
  grid-area: footer;
  background: #203040;
  color: #ffffff;
}
/* common */

h1{
  font-size: 1.8rem;
  padding: 1rem 0;
}

h2{
  font-size: 1.6rem;
  padding: 1rem 0;
}

a{
  text-decoration: none;
}
a:hover{
  color: #ff8000;
}
header a{
  color: #ffffff;
  padding: 1rem;
}
/* brand */
a.brand{
  color: #ffffff;
  font-size: 3rem;
  font-weight: bold;
}

.row{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.row.center{
  justify-content: center;
}

/* images */
img{
  border-radius: 0.5rem;
}

img.medium{
  max-width: 18rem;
  width: 100%;
}
/* card */
.card{
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem ;
  margin: 1rem;
}

.card-body{
  padding: 1rem;
}
.card-body>*{
  margin-bottom: 0.5rem;
}

.price{
  font-size: 2rem;
}

/* rating */

.rating span{
  color: #f0c040;
  margin: 0.1rem;
}
